/* registro.jsx */
/* -------------------*/
import React, { useState } from "react"; // Importa React y el hook useState para manejar estados locales.
import { useNavigate } from "react-router-dom"; // Importa useNavigate para redirigir entre rutas.
import "../../assets/scss/registro.scss"; // Importa el archivo SCSS para los estilos especficos de este componente.

const BASE_IMG_URL = "./assets/img"; // Define la URL base para las imgenes utilizadas en el componente.

const Registro = () => { // Declara el componente funcional Registro.
  const navigate = useNavigate(); // Inicializa useNavigate para manejar la navegacin.
  const [showModal, setShowModal] = useState(false); // Define el estado para controlar la visibilidad del modal.

  // Maneja el envo del formulario.
  const handleSubmit = (event) => {
    event.preventDefault(); // Previene el comportamiento predeterminado del formulario (recargar la pgina).
    setShowModal(true); // Muestra el modal al completar el registro.
  };

  // Maneja el cierre del modal.
  const handleCloseModal = () => {
    setShowModal(false); // Oculta el modal.
    navigate("/login"); // Redirige al usuario a la pgina de inicio de sesin.
  };

  return (
    <div className="registro-outer-container"> {/* Contenedor principal para el diseo del registro. */}
      <div className="registro-container"> {/* Contenedor interno que divide la pantalla en dos secciones. */}
        
        {/* Contenedor izquierdo con contenido de bienvenida. */}
        <div className="left-container"> 
          <div className="welcome-content"> {/* Seccin para imgenes y texto de bienvenida. */}
            <img
              src={`${BASE_IMG_URL}/icon.png`} // Imagen del logotipo.
              alt="Logo SOS911"
              className="welcome-image"
            />
            <h1 className="login-title"> {/* Ttulo principal con estilos personalizados. */}
              <span className="sos-text">Sos</span>
              <span className="nine-eleven-text">911</span>
            </h1>
            <p className="welcome-text"> {/* Texto descriptivo. */}
              Un toque para tu seguridad.
            </p>
            <img
              src={`${BASE_IMG_URL}/person.jpg`} // Imagen representativa de una persona.
              alt="Persona representativa"
              className="welcome-person-image"
            />
          </div>
        </div>

        {/* Contenedor derecho con el formulario de registro. */}
        <div className="right-container">
          <h2 className="titulo">Registro</h2> {/* Ttulo de la seccin de registro. */}
          <form onSubmit={handleSubmit}> {/* Formulario que ejecuta handleSubmit al enviarse. */}
            
            {/* Grupo de entrada para el nombre. */}
            <div className="form-group">
              <label className="etiqueta">Nombre *</label>
              <input
                type="text" // Campo de texto para el nombre.
                placeholder="Ingresa tu nombre"
                required
                className="entrada"
              />
            </div>

            {/* Grupo de entrada para el apellido. */}
            <div className="form-group">
              <label className="etiqueta">Apellido *</label>
              <input
                type="text" // Campo de texto para el apellido.
                placeholder="Ingresa tu apellido"
                required
                className="entrada"
              />
            </div>

            {/* Grupo de entrada para el correo electrnico. */}
            <div className="form-group">
              <label className="etiqueta">Correo Electrnico *</label>
              <input
                type="email" // Campo de texto para correo electrnico.
                placeholder="Ingresa tu correo"
                required
                className="entrada"
              />
            </div>

            {/* Grupo de entrada para la contrasea. */}
            <div className="form-group">
              <label className="etiqueta">Contrasea *</label>
              <input
                type="password" // Campo para ingresar la contrasea.
                placeholder="Crea una contrasea"
                required
                className="entrada"
              />
            </div>

            {/* Grupo de entrada para repetir la contrasea. */}
            <div className="form-group">
              <label className="etiqueta">Repite tu contrasea *</label>
              <input
                type="password" // Campo para repetir la contrasea.
                placeholder="Repite la contrasea"
                required
                className="entrada"
              />
            </div>

            {/* Grupo de entrada para la cdula. */}
            <div className="form-group">
              <label className="etiqueta">Cdula *</label>
              <input
                type="text" // Campo de texto para la cdula.
                placeholder="Ingresa tu cdula"
                required
                className="entrada"
              />
            </div>

            {/* Grupo de entrada para la direccin. */}
            <div className="form-group">
              <label className="etiqueta">Direccin *</label>
              <input
                type="text" // Campo de texto para la direccin.
                placeholder="Ingresa tu direccin"
                required
                className="entrada"
              />
            </div>

            {/* Grupo para aceptar los trminos y condiciones. */}
            <div className="checkbox-group">
              <input type="checkbox" required className="checkbox" /> {/* Checkbox obligatorio. */}
              <p>
                Al hacer clic en Registrarse, acepta nuestros{" "}
                <a href="/terms" className="enlace-terminos">
                  Trminos
                </a>{" "}
                y que ha ledo nuestra{" "}
                <a href="/privacy-policy" className="enlace-terminos">
                  Poltica de datos
                </a>.
              </p>
            </div>

            <button type="submit" className="boton"> {/* Botn para enviar el formulario. */}
              Crear Cuenta
            </button>
          </form>

          {/* Enlace para redirigir a la pgina de inicio de sesin. */}
          <p className="texto-enlace">
            Ya eres miembro?{" "}
            <a href="/login" className="enlace">
              Haz clic aqu para iniciar sesin
            </a>
          </p>
        </div>
      </div>

      {/* Modal que se muestra al completar el registro. */}
      {showModal && (
        <div className="modal-overlay"> {/* Fondo del modal. */}
          <div className="modal"> {/* Contenedor del modal. */}
            <p className="modal-texto">Usuario creado exitosamente!</p> {/* Mensaje del modal. */}
            <button className="boton-cerrar" onClick={handleCloseModal}> {/* Botn para cerrar el modal. */}
              Aceptar
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Registro; // Exporta el componente Registro.
