/* estadisticas.jsx */
/* -------------------*/
import React from "react"; // Importa React para crear componentes
import { Panel, PanelHeader, PanelBody } from "../../components/panel/panel.jsx"; // Importa los componentes de Panel para estructurar la interfaz
import { Bar, Line, Pie } from "react-chartjs-2"; // Importa los grficos de barras, lneas y pie desde Chart.js
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
    PointElement,
    LineElement,
    ArcElement,
} from "chart.js"; // Importa mdulos especficos de Chart.js para registrar y crear grficos
import "../../assets/scss/estadisticas.scss"; // Importa los estilos de la pgina

// Registra los mdulos de Chart.js para que estn disponibles al usar los grficos
ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
    PointElement,
    LineElement,
    ArcElement
);
 // Definimos las variables de los datos con un guion bajo al inicio para mayor seguridad
 
const Estadisticas = () => { // Define el componente de la pgina de estadsticas
    const _datosUsuarios = { // Datos para el grfico de barras de gestin de usuarios
        labels: ["Administradores", "Clientes"], // Etiquetas de las categoras
        datasets: [
            {
                label: "Cantidad de Usuarios", // Ttulo del grfico
                data: [10, 40], // Datos de la cantidad de usuarios
                backgroundColor: "rgba(54, 162, 235, 0.6)", // Color de fondo de las barras
            },
        ],
    };

    const _datosRespuestas = { // Datos para el grfico de lneas de respuestas de usuarios
        labels: ["911", "SOS", "Innecesario"], // Etiquetas de categoras
        datasets: [
            {
                label: "Actividad de Usuarios", // Ttulo del grfico
                data: [100, 150, 50], // Datos de la actividad
                borderColor: "rgba(255, 99, 132, 1)", // Color de la lnea
                fill: false, // No llenar el rea bajo la lnea
            },
        ],
    };

    const _datosDispositivos = { // Datos para el grfico de torta (Pie) de dispositivos utilizados
        labels: ["Android", "iOS", "Otros"], // Etiquetas de categoras
        datasets: [
            {
                data: [60, 30, 10], // Datos del porcentaje de dispositivos
                backgroundColor: ["#FF6384", "#36A2EB", "#FFCE56"], // Colores para cada sector
            },
        ],
    };

    const _datosNotificacionesCombinados = { // Datos combinados para grfico de barras y lneas de notificaciones
        labels: ["Total Notificaciones", "Botn Pulsado", "Clientes Notificados"], // Etiquetas de categoras
        datasets: [
            {
                type: "bar", // Tipo de grfico: barra
                label: "Notificaciones del Sistema", // Ttulo del grfico
                data: [1000, 700, 500], // Datos de las notificaciones
                backgroundColor: "rgba(255, 99, 132, 0.6)", // Color de fondo de las barras
            },
            {
                type: "line", // Tipo de grfico: lnea
                label: "Tendencia", // Ttulo de la lnea
                data: [1000, 700, 500], // Datos de la tendencia
                borderColor: "rgba(54, 162, 235, 1)", // Color de la lnea
                borderWidth: 2, // Grosor de la lnea
                fill: false, // No llenar el rea bajo la lnea
            },
        ],
    };

    const _datosUbicaciones = { // Datos para el grfico de barras de gestin de ubicaciones
        labels: ["Zona Norte", "Zona Centro", "Zona Sur"], // Etiquetas de las zonas
        datasets: [
            {
                label: "Cantidad de Eventos", // Ttulo del grfico
                data: [400, 350, 250], // Datos de los eventos por zona
                backgroundColor: "rgb(7, 211, 184)", // Color de fondo de las barras
            },
        ],
    };

    const _datosBotonesEmergencia = { // Datos para el grfico de torta (Pie) de botones de emergencia
        labels: ["Activados", "No Activados"], // Etiquetas de las categoras
        datasets: [
            {
                label: "Estado de Botones de Emergencia", // Ttulo del grfico
                data: [120, 80], // Datos de los botones activados y no activados
                backgroundColor: ["#4BC0C0", "#FF9F40"], // Colores para cada sector
            },
        ],
    };

    const _datosClientes = { // Datos para el grfico de torta (Pie) de estado de clientes
        labels: ["Activos", "Inactivos"], // Etiquetas de las categoras
        datasets: [
            {
                label: "Estado de Clientes", // Ttulo del grfico
                data: [500, 200], // Datos de los clientes activos e inactivos
                backgroundColor: ["#9966FF", "#FFDD57"], // Colores para cada sector
            },
        ],
    };

    const _datosGrupos = { // Datos para el grfico de barras de gestin de grupos
        labels: ["Grupo A", "Grupo B", "Grupo C"], // Etiquetas de los grupos
        datasets: [
            {
                label: "Cantidad de Grupos", // Ttulo del grfico
                data: [5, 8, 3], // Datos de la cantidad de grupos
                backgroundColor: "rgb(29, 165, 228)", // Color de fondo de las barras
            },
        ],
    };

    return (
        <div className="estadisticas-container"> {/* Contenedor principal de la pgina de estadsticas */}
            <h1 className="page-header">
                Reportes y Estadsticas <small>Estadsticas Generales</small> {/* Ttulo principal de la pgina */}
            </h1>
            <div className="row"> {/* Fila para organizar los grficos */}
                <div className="col-md-6"> {/* Columna para el grfico de gestin de usuarios */}
                    <Panel> {/* Componente Panel */}
                        <PanelHeader>
                            <h4 className="panel-title">Gestin de Usuarios</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Bar data={_datosUsuarios} /> {/* Grfico de barras de gestin de usuarios */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6"> {/* Columna para el grfico de respuestas de usuarios */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Respuestas de Usuarios</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Line data={_datosRespuestas} /> {/* Grfico de lnea de respuestas */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6 mt-4"> {/* Columna para el grfico de dispositivos utilizados */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Dispositivos Utilizados</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Pie data={_datosDispositivos} /> {/* Grfico de torta de dispositivos */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6 mt-4"> {/* Columna para el grfico de gestin de notificaciones */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Gestin de Notificaciones</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Bar data={_datosNotificacionesCombinados} /> {/* Grfico combinado de notificaciones */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6 mt-4"> {/* Columna para el grfico de gestin de ubicaciones */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Gestin de Ubicaciones</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Bar data={_datosUbicaciones} /> {/* Grfico de barras de ubicaciones */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6 mt-4"> {/* Columna para el grfico de botones de emergencia */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Botones de Emergencia</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Pie data={_datosBotonesEmergencia} /> {/* Grfico de torta de botones de emergencia */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6 mt-4"> {/* Columna para el grfico de estado de clientes */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Clientes</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Pie data={_datosClientes} /> {/* Grfico de torta de estado de clientes */}
                        </PanelBody>
                    </Panel>
                </div>
                <div className="col-md-6 mt-4"> {/* Columna para el grfico de gestin de grupos */}
                    <Panel>
                        <PanelHeader>
                            <h4 className="panel-title">Grupos</h4> {/* Ttulo del panel */}
                        </PanelHeader>
                        <PanelBody>
                            <Bar data={_datosGrupos} /> {/* Grfico de barras de grupos */}
                        </PanelBody>
                    </Panel>
                </div>
            </div>
        </div>
    );
};

export default Estadisticas; // Exporta el componente para usarlo en otras partes del proyecto
